*{
    margin: 0;
    padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  padding: 0;
}
#header{
  height: 40px;
}
.mint-button-icon{
   margin-top: 15px;
   /*padding-top: 10px;*/
   padding-left: 10px;
}
.mintui-back:before{
    /*margin-top: 3px;*/
    position: absolute;
    left: 0;
    top: 10px;
}
label{
    margin-bottom: 0;
}
  /* 轮播图*/
#swipeMy{
    height: 180px;
    width: 100%;
}
#swipeMy img{
    width: 100%;
}
  /* center  */
#center{
  /*margin-top: 60px;*/
  margin-bottom: 80px;
}
  /* center 图标和字体设置*/
#center ul li span {
    font-size: 30px;
}
#center ul li:nth-child(1) span {
    color: blueviolet;
}
#center ul li:nth-child(2) span {
    color: greenyellow;
}
#center ul li:nth-child(3) span {
    color: red;
}
#center ul li:nth-child(4) span {
    color: gray;
}
#center ul li:nth-child(5) span {
    color: indigo;
}
  /* 底部通栏*/
#footer  .mint-tabbar.is-fixed{

    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
    height: 80px;
    background-color: #26a2ff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.mint-tab-item{
    height: 80px;
}
  /* footer 图标设置 t*/
.mint-tab-item-label  span:nth-child(1){

    text-align: center;
    line-height: 30px;
    color: white;
    height: 30px;
    width: 100%;
    font-size: 30px;
}
  /* footer 字体设置*/
.mint-tab-item-label  span:nth-child(2){
  padding-top: 5px;
  color: whitesmoke;
  font-weight: 700;
  font-size: 15px;
  display: block;
  margin-top: 5px;
  text-decoration: none;
}
.mint-tab-item-label{
    padding-top: 10px;
}
.favorite{
    position: relative;
}
.favorite .favor_count{
    position: absolute;
    top: 10px;
    right: 10px;
    height: 20px;
    width: 20px;
    text-align: center;
    line-height: 10px;
    background-color: red;
    border-radius: 50%;
    /*display: block;*/
    padding: 0;
}
.v-enter{
    opacity: 0;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.v-leave-to{
    opacity: 0;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    position: absolute;
}
.v-enter-active,
  .v-leave-active{
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    /*position: absolute;*/
}






































































































